import React from "react";
import img1 from "../../assets/poert1.png";
import img2 from "../../assets/port2.png";
import img3 from "../../assets/port3.png";
import { LazyLoadImage } from "react-lazy-load-image-component";

export default function Portfolio() {
  return (
    <>
    <section className="portfolio d-flex justify-content-center align-items-center m-5 p-5">
      <div className="container">
        <div className="headTitle txt-dark">
            <h1 className=" text-center text-uppercase fw-bolder">Portfolio Component</h1>
            <div className="my-2 mx-auto d-flex justify-content-center align-items-baseline">
                <p className="underLine bg-darkColor"></p>
                <i className="fa-solid fa-star mx-3"></i>
                <p className="underLine bg-darkColor"></p>
            </div>
        </div>
        <div className="row g-5">
          <div className="col-lg-4 col-md-6">
            <div className="card rounded-3 overflow-hidden">
              <LazyLoadImage src={img1} className="w-100" />
              <div className="overlay">
                  <i className="fa-solid fa-plus fa-6x txt-color"></i>
              </div>
            </div>
          </div>
          <div className="col-lg-4 col-md-6">
            <div className="card rounded-3 overflow-hidden">
              <LazyLoadImage src={img2} className="w-100" />
              <div className="overlay">
                  <i className="fa-solid fa-plus fa-6x txt-color"></i>
              </div>
            </div>
          </div>
          <div className="col-lg-4 col-md-6">
            <div className="card rounded-3 overflow-hidden">
              <LazyLoadImage src={img3} className="w-100" />
              <div className="overlay">
                  <i className="fa-solid fa-plus fa-6x txt-color"></i>
              </div>
            </div>
          </div>
          <div className="col-lg-4 col-md-6">
            <div className="card rounded-3 overflow-hidden">
              <LazyLoadImage src={img1} className="w-100" />
              <div className="overlay">
                  <i className="fa-solid fa-plus fa-6x txt-color"></i>
              </div>
            </div>
          </div>
          <div className="col-lg-4 col-md-6">
            <div className="card rounded-3 overflow-hidden">
              <LazyLoadImage src={img2} className="w-100" />
              <div className="overlay">
                  <i className="fa-solid fa-plus fa-6x txt-color"></i>
              </div>
            </div>
          </div>
          <div className="col-lg-4 col-md-6">
            <div className="card rounded-3 overflow-hidden">
              <LazyLoadImage src={img3} className="w-100" />
              <div className="overlay">
                  <i className="fa-solid fa-plus fa-6x txt-color"></i>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
    </>
  );
}
